</template>
<script>
	export default {
	  data() {
	    return {
	      // 
	      type: 1,
	      // 
	      emoji: '',
	      // 
	      height: 27,
	      isShowToast: 'none'
	    }
	  },
	  computed: {
	    width() {
	      return this.height
	    }
	  },
	  methods: {
	    chooseEmoji(e) {
	      // console.log(e.target.alt);
	      if (e.target.alt) {
	        // altalt
	        this.emoji = e.target.alt
	      } else {
	        this.emoji = 'No point to the emoji, please click again'
      }
	    },
	    copyCode() {
	      // 
	      var input = document.getElementById('emojiCode')
	      // console.log(input);
	      // input.focus();
	      input.select()
	      document.execCommand('Copy')
	      // 
	      this.isShowToast = 'block'
	      var self = this
	      setTimeout(function() {
	        self.isShowToast = 'none'
	      }, 1000)
	    }
	  }
	}
</script>
<style scoped="scoped">
@import 'weui';
	.page__hd {
		padding: 40px;
	}
	
	.page__title {
		text-align: left;
		font-size: 20px;
		font-weight: 400;
	}
	
	.page__desc {
		margin-top: 5px;
		color: #888;
		text-align: left;
		font-size: 14px;
	}
	
	.page,
	body {
		background-color: #f8f8f8;
	}
	
	.page__bd_spacing {
		padding: 0 15px;
	}
	
	.page__bd_spacing_top {
		padding-top: 15px;
	}
	
	input[type=range] {
		width: 100%;
		-webkit-appearance: none;
		background: #f8f8f8;
	}
	
	input[type=range]::-webkit-slider-thumb {
		-webkit-appearance: none;
		height: 25px;
		width: 25px;
		margin-top: -5px;
		/**/
		background: #ffffff;
		border-radius: 50%;
		/**/
		border: solid 0.125em rgba(205, 224, 230, 0.5);
		/**/
		box-shadow: 0 .125em .125em #3b4547;
		/**/
	}
</style>


